<template>
  <div class="bg">
    <BaseNavBar :title="title"></BaseNavBar>
    <van-cell-group>
      <van-cell center :title="item.text" is-link v-for="item in dataArr" :key="item.name" @click="onClick(item)" />
    </van-cell-group>

    <van-action-sheet v-model="isShowActionSheet" :actions="actionsArr" description="" @select="onSelect" cancel-text="" close-on-click-action />

  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      title: "",
      isShowActionSheet: false,
      actionsArr: [{ name: '', color: '#ee0a24' },],
      dataArr: [
        { text: "", name: "" },
        { text: "", name: "" },
      ],
    };
  },
  methods: {
    onClick(item) {
      console.log(JSON.stringify(item));
      console.log(item.text);
      if (item.text === '') {
        this.isShowActionSheet = true;
        return
      }
      //   if (item.name) {
      //     this.$router.push({ name: item.name });
      //   }
    },
    onSelect(item) {
      console.log(item);
      this.isShowActionSheet = false;
      this.$toast.loading({
        duration: 0,
        message: '...',
      });
      setTimeout(() => {
        this.$toast.success('');
        window.localStorage.removeItem('userInfo');
        this.$router.replace({ name: "Login" });
        this.$toast.clear();
      }, 2000);
    },
  },
  created() { },
};
</script>

